"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"

export function SymptomChecker() {
  const [step, setStep] = useState(1)
  const [symptoms, setSymptoms] = useState("")
  const [isRecording, setIsRecording] = useState(false)
  const [formData, setFormData] = useState({
    age: "",
    gender: "",
    height: "",
    weight: ""
  })

  const handleStartRecording = () => {
    setIsRecording(true)
    // TODO: Implement voice recording
  }

  const handleStopRecording = () => {
    setIsRecording(false)
    // TODO: Stop recording and process voice
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (step === 1) {
      setStep(2)
    } else {
      // TODO: Submit to backend
      console.log("Submitting:", { symptoms, ...formData })
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4">
      <Card className="max-w-2xl mx-auto bg-white shadow-xl rounded-2xl overflow-hidden transition-all duration-300 animate-fade-in">
        <div className="bg-[#0066FF] px-6 py-4">
          <div className="flex items-center justify-between">
            <h2 className="text-white font-semibold text-lg">
              {step === 1 ? "¿Qué te ocurre?" : "Información adicional"}
            </h2>
            <span className="text-blue-100 text-sm bg-blue-600/30 px-3 py-1 rounded-full">
              Paso {step} de 2
            </span>
          </div>
        </div>

        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {step === 1 ? (
            <div className="space-y-4 animate-fade-in">
              <div className="space-y-2">
                <Label htmlFor="symptoms" className="text-lg font-semibold text-gray-700">
                  Describe tus síntomas
                </Label>
                <div className="relative">
                  <Textarea
                    id="symptoms"
                    placeholder="Describe tus síntomas con el mayor detalle posible..."
                    value={symptoms}
                    onChange={(e) => setSymptoms(e.target.value)}
                    className="h-32 resize-none text-gray-700 border-gray-200 focus:border-[#0066FF] focus:ring-[#0066FF] rounded-xl shadow-sm"
                  />
                  <Button
                    type="button"
                    variant={isRecording ? "destructive" : "outline"}
                    onClick={isRecording ? handleStopRecording : handleStartRecording}
                    className={`absolute bottom-2 right-2 rounded-full transition-all duration-200 ${
                      isRecording 
                        ? 'animate-pulse bg-red-500 hover:bg-red-600 text-white border-red-500' 
                        : 'hover:bg-[#0066FF]/5 border-[#0066FF] text-[#0066FF]'
                    }`}
                    size="sm"
                  >
                    <svg className="h-5 w-5 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z" />
                    </svg>
                    {isRecording ? "Detener" : "Dictar"}
                  </Button>
                </div>
                <p className="text-sm text-gray-500 mt-2">
                  Puedes escribir o usar el micrófono para describir tus síntomas
                </p>
              </div>

              <div className="flex justify-end pt-4">
                <Button 
                  type="submit" 
                  className="bg-[#0066FF] hover:bg-[#0052cc] text-white px-8 py-2.5 rounded-full transition-all duration-200 transform hover:scale-105 shadow-md hover:shadow-lg disabled:opacity-50 disabled:hover:scale-100"
                  disabled={!symptoms.trim()}
                >
                  Continuar
                </Button>
              </div>
            </div>
          ) : (
            <div className="space-y-6 animate-fade-in">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <Label htmlFor="age" className="font-medium text-gray-700">
                    Edad
                  </Label>
                  <Input 
                    id="age"
                    name="age"
                    type="number" 
                    placeholder="Ej: 35" 
                    value={formData.age}
                    onChange={handleInputChange}
                    className="border-gray-200 focus:border-[#0066FF] focus:ring-[#0066FF] rounded-xl shadow-sm"
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="gender" className="font-medium text-gray-700">
                    Género
                  </Label>
                  <select
                    id="gender"
                    name="gender"
                    value={formData.gender}
                    onChange={handleInputChange}
                    className="w-full rounded-xl border-gray-200 focus:border-[#0066FF] focus:ring-[#0066FF] shadow-sm"
                    required
                  >
                    <option value="">Seleccionar</option>
                    <option value="M">Masculino</option>
                    <option value="F">Femenino</option>
                    <option value="O">Otro</option>
                  </select>
                </div>
                <div className="space-y-2">
                  <Label htmlFor="height" className="font-medium text-gray-700">
                    Altura (cm)
                  </Label>
                  <Input 
                    id="height"
                    name="height"
                    type="number" 
                    placeholder="Ej: 170" 
                    value={formData.height}
                    onChange={handleInputChange}
                    className="border-gray-200 focus:border-[#0066FF] focus:ring-[#0066FF] rounded-xl shadow-sm"
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="weight" className="font-medium text-gray-700">
                    Peso (kg)
                  </Label>
                  <Input 
                    id="weight"
                    name="weight"
                    type="number" 
                    placeholder="Ej: 70" 
                    value={formData.weight}
                    onChange={handleInputChange}
                    className="border-gray-200 focus:border-[#0066FF] focus:ring-[#0066FF] rounded-xl shadow-sm"
                    required
                  />
                </div>
              </div>

              <div className="flex justify-end space-x-4 pt-4">
                <Button 
                  type="button" 
                  variant="outline" 
                  onClick={() => setStep(1)}
                  className="border-[#0066FF] text-[#0066FF] hover:bg-[#0066FF]/5 rounded-full px-8 py-2.5 transition-all duration-200"
                >
                  Atrás
                </Button>
                <Button 
                  type="submit" 
                  className="bg-[#0066FF] hover:bg-[#0052cc] text-white px-8 py-2.5 rounded-full transition-all duration-200 transform hover:scale-105 shadow-md hover:shadow-lg"
                >
                  Evaluar síntomas
                </Button>
              </div>
            </div>
          )}
        </form>
      </Card>
    </div>
  )
}
